import React, { Component, useRef } from "react";
import ReactPlayer from 'react-player'
import { Redirect, Link } from 'react-router-dom'
import axios from "axios";
import { toast } from "react-toastify";
import { format } from "react-string-format";
import { Dropdown, DropdownButton } from "react-bootstrap";


import ReactCrop from 'react-image-crop';

import 'react-image-crop/dist/ReactCrop.css';
import './Save.css';
import { saveAs } from 'file-saver';


class Save extends Component {
    constructor(props) {
        super(props);

    }
    state = {
        shouldRedirect: false,
        url: null,
        authed: false
    }
    uploadGoogleDrive = () => {
        axios
            .get(
                `${process.env.REACT_APP_API_URL}/googleDrive/upload`)
            .then((res) => {
                if (res.status == 200) {
                    this.setState({
                        shouldRedirect: true,
                    })
                }
            }).catch((error) => {
                console.log(error)
            });
    }
    download = () => {
        saveAs(
            `${process.env.REACT_APP_PUBLIC_URL}/editedVideos/${this.props.location.state.savedVideo}`,
            this.props.location.state.savedVideo
        )
    }
    componentDidMount() {
        axios
            .get(
                `${process.env.REACT_APP_API_URL}/googleDrive/?fileName=${this.props.location.state.savedVideo}`)
            .then((res) => {
                if (res.status == 200) {
                    console.log('res')
                    console.log(res)
                    if (res.data.authed) {
                        this.setState({
                            authed: true
                        })
                    } else {
                        this.setState({
                            url: res.data.url,
                            authed: false
                        })
                    }

                }
            }).catch((error) => {
                console.log(error)
            });
    }

    render() {
        console.log('this.props')
        console.log(this.props)
        if (this.state.shouldRedirect) {
            return <Redirect
                to={{
                    pathname: '/',
                }}
            />
        }

        return (

            <div>
                <div style={{ display: 'flex', width: "100%" }}>

                    {
                        this.props.location.state.savedVideo
                            ?
                            <video
                                style={{ marginRight: "auto", marginLeft: "auto" }}
                                autoPlay
                                controls
                                src={`${process.env.REACT_APP_PUBLIC_URL}/editedVideos/${this.props.location.state.savedVideo}`}
                            >
                            </video>
                            :
                            null
                    }
                </div>
                <div style={{ textAlign: 'center', padding: "30px" }}>
                    <Link to="/">
                        <button>
                            go to home
                        </button>
                    </Link>
                </div>
                <div>
                    <button onClick={this.download}>
                        download
                    </button>
                    <DropdownButton id="dropdown-basic-button" title="upload">
                        {
                            !this.state.authed 
                            ?
                            <a href={this.state.url}>
                            Google drive
                            </a>
                            :
                            <button onClick={this.uploadGoogleDrive}>
                                Google drive
                            </button>
                        }
                       
                       
                    </DropdownButton>
            </div>
            </div >




        )
    }
}

export default Save;
